.nav{
  background-color: black;
  margin: 0;
  width: 100%;
  padding: 10px;
}
.wrapper{

  background-color: #DDD5DF;
}
.NavLink{
  color: white;
}
.section1{
  display: block;
  height: 400px;
  width: 100%;
  background-image: linear-gradient(to right, #21B2B0, #194F8B);
  margin: 0;
}
.heading{
  height: 150px;
  width: 70%;
  position: relative;
  top: 30%;
  left: 10%;
  font-size: 40px;
}
.body{
  position: relative;
  top: 35%;
  left: 10%;
  font-size: 30px;
}
.Col{
  height: 170px;
}
h2{
  margin-left: 15px;
}
.col1{
  padding: 30px 10px 10px 30px;
  background-color: #172C43;
  color: white;
}
.icon{
  position: relative;
  top: 30%;
  left: 40%;
}
.col2, .col3{
  padding: 40px;
  background-color: #E7388C;;
}
.col3{
  background-color: #7A348D;
}
.image1, .image2{
  height: 170px;
  width: inherit;
}

.section3{
  display: flex;
  justify-content: space-around;
  text-align: left;
}
h2{
  margin: 10px 0 20px 20px;
}
p{
  width: 45%;
}
#close-icon {
    width: 30px;
    height: 35px;
    position: relative;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    cursor: pointer;
  }
  
#close-icon span {
    display: block;
    height: 2px;
    width: 100%;
    background: #E9E1EB;
    border-radius: 2px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    -moz-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
    margin-top: 5px;
  }


  /* Close Icon */

#close-icon {
    padding: 2px;
}

#close-icon span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#close-icon span:nth-child(2) {
  top: 18px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#close-icon span:nth-child(3) {
  top: 36px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#close-icon.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 1px;
    left: 1px;
    position: absolute;
}

#close-icon.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#close-icon.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 22px;
  left: 1px;
  position: absolute;
}
#hamburger1{
  display: none;
}
@media screen and (max-width: 800px){
  .wrapper{
    display: inline-flex;
    flex-direction: column;
  background-color: #DDD5DF;
  }
  .navbar{
    display: none;
  }
  #hamburger1{
    display: block;
    cursor: pointer;
  }
  .Col{
    height: 240px;
  }
  .image1, .image2{
    height: 240px;
  }
  .section3{
    display: block;
  }
  p{
    width: 100%;
  }
  .navbar2{
    display: block;

  }
}
